export default {
	data() {
		return {
			currentNum:0,
			stu:null,
			dataList:null
		}
	},
	methods: {
		next()
		{
			this.currentNum++;
			this.stu = this.dataList[this.currentNum]
		}
		
	},
	computed:{
		hasPrevent:function(){
			return{
				'bg-red': true
			}
		}
	},
	onShow() {
		var me = this;
		uni.request({
			url: this.serverUrl + '/stu/mystudent/0/1?name=',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data)
				this.dataList = res.data
				this.stu = res.data[0]
			},
			fail: () => {},
			complete: () => {}
		});
	}
	
}
